<template>
  <div class="hello container">
    <div class="row ">
      <h1 class="image"><img :src="title" alt=""></h1>
    </div>
    <div class="row">
      <Input class="input" v-model="input" @keyup.enter.native="isData" placeholder="请输入关键字">
      <Button slot="append" icon="ios-search" type="primary" @click="isData">搜索</Button>
      </Input>
    </div>
    <div class="row">
      <Table :columns="columns" :data="data"></Table>
    </div>
    <div class="page">
      <div class="row">
        <Page :total="total" :page-size="pageNum" :current="current" size="small" @on-change="isChange"
              @on-page-size-change="isSizeChange"
              show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    data () {
      return {
        input: '',
        total: 0,
        columns: [{
          title: '图片',
          key: 'img',
          render: (h, params) => {
            return h('div', [
              h('img', {
                attrs: {
                  src: params.row.img
                }
              })
            ])
          }
        }],
        data: [],
        title: '',
        current: 1,
        pageNum: 10
      }
    },
    mounted () {
//      this.isData()
    },
    methods: {
      isData () {
        this.$http.get('/search?key=' + this.input + '&current=' + this.current + '&pageNum=' + this.pageNum, {}).then((req) => {
          this.data = []
          if (req.body.code === '200') {
            this.data = req.body.list
            this.total = req.body.total
          } else {
          }
          console.log(req.body)
        })
      },
      isChange (n) {
        this.current = n
        this.isData()
      },
      isSizeChange (n) {
        this.pageNum = n
        this.current = 1
        this.isData()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .image {
    background-color: #000;
  }

  .input {
    max-width: 400px;
    margin: 0 auto;
  }

  .row {
    padding-top: 20px;
  }

  .container {
    padding-bottom: 150px;
  }

  .ivu-table-cell {
    padding: 18px;
  }

  .ivu-table-cell img {
    max-width: 100%;
  }

  .page {
    z-index: 99999;
    width: 100%;
    position: fixed;
    text-align: center;
    padding-bottom: 20px;
    background-color: #fff;
    /*border-top:1px solid #e9eaec;*/
    box-shadow: 0 0 10px #dddee1;
    left: 0;
    bottom: 0;
  }

  .page .ivu-select-dropdown {
    top: auto !important;
    bottom: 50px !important;
  }
</style>



