<template>
  <div class="hello container">
    <div class="row">
      <Button type="primary" @click="$router.push('/image')">百度图片</Button>
      <hr>
      <Slider v-model="value" range></Slider>
      <hr>
      <Progress :percent="num" status="active"></Progress>
      <hr>
      <i-switch></i-switch>
      <hr>
      <Icon type="checkmark"/>
      <hr>
      <h2>{{msg}}</h2>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        value: [],
        num: 0
      }
    },
    mounted () {
      setInterval(() => {
        this.num++
        if (this.num > 100) this.num = 0
      }, 200)
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



